import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutusComponent } from './aboutus/aboutus.component';
import { AllprintsComponent } from './allprints/allprints.component';
import { AlltoursComponent } from './alltours/alltours.component';
import { ContactusComponent } from './contactus/contactus.component';
import { DayContentComponent } from './day-content/day-content.component';
import { GalleryComponent } from './gallery/gallery.component';
import { HomeComponent } from './home/home.component';
import { OrderDetailsComponent } from './order-details/order-details.component';
import { OrderComponent } from './order/order.component';
import { PrintsComponent } from './prints/prints.component';
import { ProductviewComponent } from './productview/productview.component';
import { SafariDetailsTwoComponent } from './safari-details-two/safari-details-two.component';
import { SafariDetailsComponent } from './safari-details/safari-details.component';
import { WebSafariDetailsTwoComponent } from './web-safari-details-two/web-safari-details-two.component';
import { WebSafariDetailsComponent } from './web-safari-details/web-safari-details.component';

const routes: Routes = [
  { path: "", component: HomeComponent },
  { path: "home", component: HomeComponent },
  { path: "aboutus", component: AboutusComponent },
  { path: "prints", component: PrintsComponent },
  { path: "contact", component: ContactusComponent },
  { path: "gallery", component: GalleryComponent },
  { path: "safari-details", component: SafariDetailsComponent },
  { path: "day-content", component: DayContentComponent },
  { path: "safari-details-two", component: SafariDetailsTwoComponent },
  { path: "productview", component: ProductviewComponent },
  { path: "safari", component: WebSafariDetailsComponent },
  { path: "order", component: OrderComponent },
  { path: "orderDetails", component: OrderDetailsComponent },
  { path: "safaritwo", component: WebSafariDetailsTwoComponent },
  { path: "allprints", component: AllprintsComponent },
  { path: "alltours", component: AlltoursComponent }


];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],

  exports: [RouterModule]
})
export class AppRoutingModule { }
